<template>
    <div class="app-shell app-shell-bottom-navigation">
        <app-header
            class="app-shell-header"
            :show="appHeader.show"
            :showMenu="appHeader.showMenu"
            :showBack="appHeader.showBack"
            :showLogo="appHeader.showLogo"
            :title="appHeader.title"
            :actions="appHeader.actions"
            :loading="isPageSwitching"
            @click-logo="handleClickHeaderLogo"
            @click-back="handleClickHeaderBack">
            <template slot="logo">
                <span class="app-header-logo">新闻 - news</span>
            </template>
        </app-header>
        <app-menu-tabs :entrys="menuTabs.tabs" :show="menuTabs.show"></app-menu-tabs>
        <div class="app-view-wrapper">
            <v-progress-circular
                indeterminate
                :size="50"
                v-show="isPageLoading"
                class="app-view-loading"/>
            <transition
                :name="pageTransitionName"
                @before-enter="handleBeforeEnter"
                @after-enter="handleAfterEnter">
                <keep-alive>
                    <router-view
                        v-if="!$route.meta.notKeepAlive"
                        class="app-view"
                        :class="{
                            'app-view-with-header': appHeader.show
                        }"></router-view>
                </keep-alive>
            </transition>
            <transition
                :name="pageTransitionName"
                @before-enter="handleBeforeEnter"
                @after-enter="handleAfterEnter">
                <router-view
                    v-if="$route.meta.notKeepAlive"
                    class="app-view"
                    :class="{
                        'app-view-with-header': appHeader.show
                    }"></router-view>
            </transition>
        </div>
    </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import AppHeader from './components/appHeader';
import AppMenuTabs from './components/appMenuTabs';

export default {
    name: 'bottomNavigation',
    components: {
        AppHeader,
        AppMenuTabs
    },
    data () {
        return {};
    },
    computed: {
        ...mapGetters([
            'appHeader',
            'menuTabs',
            'isPageLoading',
            'isPageSwitching',
            'pageTransitionName'
        ])
    },
    methods: {
        ...mapActions([
            'setPageSwitching'
        ]),
        handleBeforeEnter() {
            this.setPageSwitching(true);
        },
        handleAfterEnter() {
            this.setPageSwitching(false);
        },
        handleClickHeaderBack() {
            this.$router.go(-1);
        },
        handleClickHeaderLogo() {
            this.$router.push('/');
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    
    $theme := {
        primary: #3e98f0
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    
    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom



.app-shell
    position absolute
    top 0
    right 0
    bottom 0
    left 0
    width 100%
    height 100%
    display flex
    flex-direction column

    .app-shell-header
        position fixed
        top 0
        left 0
        right 0

        .app-header-logo
            padding-left 16px
            font-size 1.2em
        //     display inline-block
        //     width 88px
        //     height $app-header-height
        //     background url(https://gss0.bdstatic.com/5foIcy0a2gI2n2jgoY3K/n/nvn/static/news/imgs/bg-news-logo_344ce44.png) no-repeat
        //     background-size 65px auto
        //     background-position center 16px

    .app-shell-footer
        position fixed
        bottom 0
        left 0
        right 0

    .app-view-wrapper
        flex 1
        position relative
        overflow hidden
        .app-view-loading
            position fixed
            top 50%
            left 50%
            transform translate(-50%, -50%)
            z-index 100
            color: $theme.primary
        .app-view
            position absolute
            top 0
            right 0
            bottom 0
            left 0
            overflow-x hidden
            overflow-y auto
            transition transform 0.4s cubic-bezier(.55, 0, .1, 1)
            background: $material-theme.bg-color
            color: $material-theme.text-color

            &.app-view-with-header
                top $app-header-height

            &.app-view-with-footer
                bottom $app-footer-height

            &.slide-left-enter
                transform translate(100%, 0)

            &.slide-right-enter
                transform translate(-100%, 0)

            &.slide-right-leave-active
                transform translate(100%, 0)

            &.slide-left-leave-active
                transform translate(-100%, 0)

</style>



